    left: 0; 
    right: 0;  
    margin-left: 0;
    z-index: 10;
    width: 100%;
    height: 1px;
    background: #000;
  }
  .bar {
    right: -20px;
    position: absolute;
    top: 0;
    margin-left: 20px;
    margin-bottom: 20px;
    z-index: 10;
    height: 100%;
    width: 1px;
    background: #000;
  }
  .circle {
    position: absolute;
    right:20px;
    top: 20px;
    width: 1.5em;
    height: 1.5em;
    border: 1px solid;
    border-radius: 50%;
    transition: 0.3s;
  }
  .circle:hover {
    background-color: #000000;
  }
  .coin1 {
    position: relative;
