  .hair {
        position: absolute;
       top:0;
       left:0;
       margin-top: -10px;
       margin-left: -10px;
       background: transparent;
       border-top: 1px solid #000;
       border-left: 1px solid #000;
       pointer-events: none;
       z-index: 15;
     }
     #mousepos {
       position: absolute;
       top:0; left:0;
       padding: 10px;
       margin: 10px;
       color: rgb(0, 0, 0);
       background: rgba(255, 255, 255, 0.541);
       z-index: 1;
       font-family: 'ItalianGaramond';
       font-size: 15px;
     }
     p .highlight::before {
      content: " ";
      display: block;
      z-index: -1;
      position: absolute;
      top: 0; right: 0; bottom: 0; left: 0;
      inset: 0 0 0 0;
      background: rgba(255, 255, 255, 0.623);
      transition: transform .5s ease;
    }
     .highlight{
      font-family: 'ItalianGaramond';
      font-size: 19px;
      position: relative;
      letter-spacing: -0.5px;
      line-height: 21px;
    }
   .highlight::before {  
      transform: scaleX(0);
      transform-origin: bottom right;
    }
    .highlight:hover::before {
      transform: scaleX(1);
      transform-origin: bottom left;
    }
    .highlight::before {
      content: " ";
      display: block;
      z-index: 3;
      position: absolute;
      top: 0; right: 0; bottom: 0; left: 0;
      inset: 0 0 0 0;
      background: rgba(255, 255, 255, 0.623);
      z-index: -1;
